<template>
  <div class="hs-container" @click="_clickExtend" ref="cell" v-if="showRoute">
    <div class="info-train-container">
      <div class="info-left-container">
        <div class="top-container">
          <div class="dep-info">
            <p>{{beTime(currentRoute.DepartureDateTime,'hh:mm')}}</p>
            <p><span v-if="language === 'CN'" :class="[{start:dept=== 1},{middle:dept=== 2},{end:dept=== 3}]">{{dept|filterPassType}}</span>{{currentRoute.DepartStationName}}</p>
          </div>
          <div class="middle-info" @click.stop="$emit('delegateForTableTime',currentRoute)">
            <p>{{currentRoute.TrainCode}}</p>
            <div>
              <p></p>
              <span>{{$t('base.Schedule')}}</span>
              <p></p>
            </div>
            <p>{{currentRoute.RunTime}}</p>
          </div>
          <div class="arr-info">
            <p>{{beTime(currentRoute.ArrivalDateTime,'hh:mm')}}<span v-if="currentRoute.ArrivalDays > 0">+{{currentRoute.ArrivalDays}}</span></p>
            <p><span v-if="language === 'CN'" :class="[{start:arr=== 1},{middle:arr=== 2},{end:arr=== 3}]">{{arr|filterPassType}}</span>{{currentRoute.ArrivalStationName}}</p>
          </div>
        </div>
        <div class="bottom-contianer">
          <div v-for="(item, index) in showSeats" :key='index'>
            <p v-if="language === 'CN'">{{item.name}}: {{item.have}}</p>
            <template v-else>
              <p>{{item.name}}</p>
              <p>{{item.have}}</p>
            </template>
          </div>
        </div>
      </div>
      <div class="info-right-container">
        <p>￥{{currentRoute.LowPrice}}<span>{{$t('base.From')}}</span></p>
      </div>
    </div>
    <!-- <div class="info-train-button-container" v-show="trainInfo.show">
      <div class="book-container" v-for="(item,index) in 3" :key="index" @click.stop="book">
        <p>一等座</p>
        <p>余100张</p>
        <p>￥533</p>
        <button>预订</button>
      </div>
    </div> -->
  </div>
</template>

<script type='text/ecmascript-6'>
import Vue from 'vue'
export default {
  props: {
    currentRoute: {
      type: Object,
      default: () => {}
    },
    filters: {
      type: Array,
      default: () => []
    },
    conditionsTime: {
      type: Number,
      default: 0
    }
  },
  filters: {
    filterPassType(val) {
      switch (val) {
        case 1:
          return Vue.prototype.$t('base.S')
        case 2:
          return Vue.prototype.$t('base.B_3')
        case 3:
          return Vue.prototype.$t('base.E')
        default:
          return Vue.prototype.$t('base.Unknown')
      }
    }
  },
  computed: {
    // 出发始过终 0-过路 1-过路终站 2-始发过路 3-始发终到
    dept() {
      if (this.currentRoute.PassByType === 2 || this.currentRoute.PassByType === 3) {
        return 1
      } else if (this.currentRoute.PassByType === 0 || this.currentRoute.PassByType === 1) {
        return 2
      }
    },
    // 到达始过终 0-过路 1-过路终站 2-始发过路 3-始发终到
    arr() {
      if (this.currentRoute.PassByType === 0 || this.currentRoute.PassByType === 2) {
        return 2
      } else if (this.currentRoute.PassByType === 1 || this.currentRoute.PassByType === 3) {
        return 3
      }
    },
    showSeatTypeCout() {
      return this.currentRoute.ShowSeats ? this.currentRoute.ShowSeats.length : 0;
    },
    showSeats() {
      let seatArr = [
        { name: '', have: '' },
        { name: '', have: '' },
        { name: '', have: '' }
      ];
      seatArr.forEach((item, index) => {
        if (this.showSeatTypeCout - 1 >= index) {
          item.name = this.currentRoute.ShowSeats[index].TypeName;
          item.have = this.currentRoute.ShowSeats[index].Count > 0 ? this.$t('base.Have') : this.$t('base.Havent');
        }
      });
      return seatArr;
    },
    showRoute() {
      if (this.currentRoute.DepartureDateTime < this.conditionsTime) {
        return false
      }
      let fn = (items, key) => {
        return items.filter(item => item.select).filter(item => item.Key === key).length === 0
      }
      let haveSelect = (items) => {
        let itemsLength = items.filter(item => item.select).length;
        return itemsLength > 0 && itemsLength !== items.length;
      }
      // 0-过路 1-过路终站 2-始发过路 3-始发终到
      let passByTypeFn = (PassByType) => {
        if (PassByType === 2 || PassByType === 3) {
          return '1';
        } else if (PassByType === 0 || PassByType === 1) {
          return '0';
        } else {
          return '-1';
        }
      }
      for (let filter of this.filters) {
        if (filter.Name === this.$t('base.TrainType') && haveSelect(filter.Items) && fn(filter.Items, this.currentRoute.TrainCategory)) {
          console.log(this.currentRoute.TrainCode + '=车次类型=' + this.currentRoute.TrainCategoryDes);
          return false;
        }
        if (filter.Name === this.$t('base.DepartureStation') && haveSelect(filter.Items) && fn(filter.Items, this.currentRoute.DepartStationCode)) {
          console.log(this.currentRoute.TrainCode + '=出发车站=' + this.currentRoute.DepartStationName);
          return false;
        }
        if (filter.Name === this.$t('base.ArrivalStation') && haveSelect(filter.Items) && fn(filter.Items, this.currentRoute.ArrivalStationCode)) {
          console.log(this.currentRoute.TrainCode + '=到达车站=' + this.currentRoute.ArrivalStationName);
          return false;
        }
        if (filter.Name === this.$t('base.Originating') && haveSelect(filter.Items) && fn(filter.Items, passByTypeFn(this.currentRoute.PassByType))) {
          console.log(this.currentRoute.TrainCode + '=始发过路=' + this.PassByTypeName[this.currentRoute.PassByType]);
          return false;
        }
        if (filter.Name === this.$t('base.DepartureTime') && haveSelect(filter.Items) && fn(filter.Items, this.currentRoute.DepartTimePeriod + '')) {
          console.log(this.currentRoute.TrainCode + '=到达车站=' + this.currentRoute.DepartTimePeriod);
          return false;
        }
        if (filter.Name === this.$t('base.ArrivalTime') && haveSelect(filter.Items) && fn(filter.Items, this.currentRoute.ArrivalTimePeriod + '')) {
          console.log(this.currentRoute.TrainCode + '=到达车站=' + this.currentRoute.DepartTimePeriod);
          return false;
        }
      }
      return true;
    }
  },
  data() {
    return {
      PassByTypeName: ['过路', '始发过路', '终站过路', '始发终到']
    };
  },
  methods: {
    // _showTimeTable() {
    //   this.$router.push({ path: '/train/timeTable' });
    // },
    _clickExtend() {
      console.log(this.$refs['cell'].getBoundingClientRect());
      this.$emit(
        'delegateForExtend',
        this.currentRoute,
        this.$refs['cell'].getBoundingClientRect().top
      );
    }
  }
};
</script>

<style lang='stylus' scoped='' type='sylesheet/stylus'>
@import '~common/stylus/variable'
@import '~common/stylus/base'
.hs-container
  width 100%
  background-color $color_white
  .info-train-container
    @extend .flex-row
    @extend .flex-between
    padding 15px 0
    margin 0px 10px
    border-bottom 1px solid $color_gray_layer
    .info-left-container
      @extend .flex-column
      width calc(100% - 75px)
      font-size 0.8rem
      .top-container
        width 100%
        @extend .flex-between
        .dep-info
          @extend .flex-column
          @extend .flex-center
          width 33.33%
          width 80px
          P
            &:nth-child(1)
              font-size 1.2rem
              font-weight bold
              line-height 1.2em
            &:nth-child(2)
              font-size 0.8rem
              line-height 1.4em
              @extend .flex-row
              @extend .flex-center
              @extend .flex-start
              >span
                @extend .flex-row
                @extend .flex-center
                height 13px
                width 13px
                border-radius 1px
                color $color_white
                font-size 0.6rem
        .middle-info
          @extend .flex-column
          @extend .flex-center
          width 33.33%
          font-size 0.6rem
          p
            &:first-child
              font-weight bold
              font-size 0.8rem
            &:last-child
              color $color_gray_text
          >div
            margin 3px 0
            @extend .flex-around
            >p
              width 12px
              height 1px
              background-color $color_gray_text
            >span
              border 1px solid $color_gray_layer
              padding 3px 5px
              border-radius 15px
              color $color_gray_text
        .arr-info
          width 33.33%
          @extend .flex-column
          @extend .flex-center
          P
            &:nth-child(1)
              font-size 1.2rem
              font-weight bold
              line-height 1.2em
              position relative
              >span
                color $color_theme
                font-size 0.8rem
                position absolute
                top -4px
                right -16px
            &:nth-child(2)
              font-size 0.8rem
              line-height 1.4em
              @extend .flex-row
              @extend .flex-center
              >span
                @extend .flex-row
                @extend .flex-center
                height 13px
                width 13px
                border-radius 1px
                color $color_white
                font-size 0.6rem
      .bottom-contianer
        @extend .flex-row
        @extend .flex-between
        width 100%
        margin-top 10px
        font-size 0.7rem
        color $color_gray_text
        line-height 1.2em
        >div
          @extend .flex-column
          @extend .flex-center
          width 33.33%
    .info-right-container
      width 75px
      text-align right
      color $color_theme
      >p
        font-size 1rem
        font-weight bold
        color $color_theme
        vertical-align middle
        >span
          font-size 0.8rem
          color $color_black_text_0
          margin-left 2px
  .start
   background-color #ee7e7e
  .middle
   background-color #81d151
  .end
   background-color #72d3df
</style>



